/* Option style floating */

.atuiMediasplayer_Audioplayer.optionFloating {
    position: fixed;
    z-index: 11;
    bottom: 20px;
    left: 10%;
    right: 10%;
    margin: 0;
    border: none;
    box-shadow: var(--atuiKernel_ShadowMedium);
    background-color: hsl(var(--atuiKernel_Color-N90) / 80%);
    -webkit-backdrop-filter: var(--atuiKernel_Blur);
            backdrop-filter: var(--atuiKernel_Blur);
    display: none;
}

.atuiMediasplayer_Audioplayer.optionFloating .atuiMediasplayer_Close {
    display: block;
}

